.projects{
    color: black;
    margin-top: 15vh;
}

.projectsHeader{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    font-family: Montserrat,helvetica neue,Helvetica,Arial,sans-serif;
}

.projectsTitle{
    font-size: 3em;
}

.shortcutMenu{
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    margin-top: 4vh;
}

.iconDiv{
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    margin:auto;
    max-width:200px;
}

.icons{
    -ms-flex: 1 1;
        flex: 1 1;
    border: solid 1px black;
    height: 20px;
    max-width: 37px;
    border-radius: 5px;
    margin: 5px;
    line-height: 20px;
    vertical-align: middle;
    font-size: 12px;
    background-color: #3e89b7;
}

.icons:hover{
    background-color: white;
    color: black;
}

.icons.active{
    background-color: rgb(56, 54, 54);
    color:white;
}

.icons.active:hover{
    color: black;
    background-color: white;
}

.projectDivs{
    display: -ms-flexbox;
    display: flex; 
    /* margin-top: 10vh; */
    /* flex-wrap: wrap; */
}
.projectDivs > div:not(.project) {
    margin:auto;
}



